h1.get-in-touch {
    color: var(--primary-color);
    font-weight: bold;
}

.contact-us-photo {
    position: absolute;
    /* border-radius: 10%; */
    left: 100px;
    height: 320px;
    width: 530px;
    top:200px;
}

.border-gradient {
    padding: 1rem;
    background: linear-gradient(to right, var(--primary-color), var(--secondary-color));
    padding: 3px; 
    /* border: 5px solid; */
    border-image-slice: 1;
    border-width: 5px;
    border-top-left-radius: 10%;
    border-bottom-right-radius: 10%;
}

 div.inputs-absolute {
